re{content:"";position:absolute;bottom:0;left:0;top:0;width:5px;background-color:#c0ca33;opacity:0;transition:opacity 0.2s ease-in}.navbar .dropdown-menu .dropdown-item:hover::before{opacity:1}.navbar .fixed-top{position:fixed;-webkit-animation:navbar-animation 0.6s;animation:navbar-animation 0.6s;background-color:#fff}.navbar .fixed-top.navbar-dark .navbar-nav .nav-link.active{color:#c0ca33}.navbar .fixed-top.navbar-dark .navbar-nav .nav-link::after{background-color:#c0ca33}.navbar-dark .navbar-nav .nav-link{position:relative;color:#333 !important;font-size:0.8rem;letter-spacing:0px;padding-right:2rem !important;font-weight:800;padding-left:24px;text-transform:uppercase;font-family:"SamsungOneLatin", "SamsungOne", "SamsungOneKorean", "SamsungSharpSans", Arial, Roboto, "Helvetica Neue", "Apple SD Gothic Neo", sans-serif}.navbar-dark .navbar-nav .nav-link:focus,.navbar-dark .navbar-nav .nav-link:hover{color:#999 !important}.content{padding:75px 0}@media screen and (max-width: 768px){.navbar-brand{margin-left:20px}.navbar-nav{padding:0 20px;background-color:#fff}.navbar.fixed-top .navbar-nav{background:transparent}}@media screen and (min-width: 767px){.navbar-dark .navbar-nav .nav-link{padding:23px 15px}.navbar-dark .navbar-nav .nav-link::after{content:"";position:absolute;bottom:15px;left:30%;right:30%;height:1px;background-color:#fff;transform:scaleX(0);transition:transform 0.1s ease-in}.navbar-dark .navbar-nav .nav-link:hover::after{transform:scaleX(1)}.dropdown-menu{min-width:200px;-webkit-animation:dropdown-animation 0.3s;animation:dropdown-animation 0.3s;transform-origin:top}}@-webkit-keyframes navbar-animation{0%{opacity:0;transform:translateY(-100%)}100%{opacity:1;transform:translateY(0)}}@keyframes navbar-animation{0%{opacity:0;transform:translateY(-100%)}100%{opacity:1;transform:translateY(0)}}@-webkit-keyframes dropdown-animation{0%{transform:scaleY(0)}75%{transform:scaleY(1.1)}100%{transform:scaleY(1)}}@keyframes dropdown-animation{0%{transform:scaleY(0)